* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background: #f1f5f8;
  color: #222;
}

.birthdayList {
  width: 90vw;
  max-width: 800px;
  margin: 5rem auto;
  display: grid;
  gap: 2rem;
  background: #fff;
  border-radius: 1rem;
  padding: 1rem 2rem;
}

@media screen and (min-width: 768px) {
  .birthdayList {
    grid-template-columns: repeat(3, 1fr);
    /* align-items: start; */
  }
}

.birthdayList h1 {
  margin-top: 0.5rem;
}
/* .book h4 {
  color: #617d98;
  font-size: 0.75rem;
  margin-top: 0.25rem;
} */
.birthdayList p {
  margin-top: 0.5rem;
}
.birthdayList img {
  border-radius: 50%;
}
